.App{
  text-align: center;
  display: flex;
  overflow-x: scroll;
}

::-webkit-scrollbar{
  display: none;
}

body{
  background-color: black;
  height: 90vh;
  width: 100vw;  
}

nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: rgb(0, 0, 0);
  font-size: 6vh;
  backdrop-filter: blur(4px);
}
a{
  font-family:'Times New Roman', Times, serif ;
  text-decoration: none;
  color: white;
  
}

